<template>
    <div class="memberItem">
        <ul class="members" v-if="members.length>0">
            <!-- 产品 -->
            <li class="jobs" v-for="">
                <h2>
                    <span class="icon job"></span>
                    <p>产品</p>
                </h2>
                <ul>
                    <li class="singleMember" v-for="(item, index) in members">
                        <span class="icon single"></span>
                        <p class="text">
                            <span>{{item.mName}}</span>
                            <span>{{item.mDay}}人日</span>
                            <span>{{item.mProcess}}%</span>
                            <span>{{item.mDate}}</span>
                        </p>
                        <span class="icon more"></span>
                    </li>
                </ul>
            </li>
        </ul>
    </div>
</template>
<script>
import {mapState} from 'vuex'
export default {
    data(){
        return {
            pId: this.$route.query.pId,
            members: [],
            sortMembers: {}
        }
    },
    computed: mapState({
        projects: 'projectData'
    }),
    watch: {
        'pId'(){
            this.members = this.projects.filter(item => {
                return item.pId = this.pId;
            })[0].members;
            console.log('*****members',this.members);
            this.members.forEach(item => {
                switch (item.title) {
                    case 1:
                        if(!this.sortMembers[1]){
                            this.sortMembers[1] = [item.mName];
                        }else{
                            this.sortMembers[1].push(item.mName);
                        }
                        break;
                    case 2:
                        if(!this.sortMembers[2]){
                            this.sortMembers[2] = [item.mName];
                        }else{
                            this.sortMembers[2].push(item.mName);
                        }
                    case 3:
                        if(!this.sortMembers[3]){
                            this.sortMembers[3] = [item.mName];
                        }else{
                            this.sortMembers[3].push(item.mName);
                        }
                        break;
                    case 4:
                        if(!this.sortMembers[4]){
                            this.sortMembers[4] = [item.mName];
                        }else{
                            this.sortMembers[4].push(item.mName);
                        }
                    case 5:
                        if(!this.sortMembers[5]){
                            this.sortMembers[5] = [item.mName];
                        }else{
                            this.sortMembers[5].push(item.mName);
                        }
                        break;
                    case 6:
                        if(!this.sortMembers[6]){
                            this.sortMembers[6] = [item.mName];
                        }else{
                            this.sortMembers[6].push(item.mName);
                        }
                    case 7:
                        if(!this.sortMembers[7]){
                            this.sortMembers[7] = [item.mName];
                        }else{
                            this.sortMembers[7].push(item.mName);
                        }
                        break;
                    case 8:
                        if(!this.sortMembers[8]){
                            this.sortMembers[8] = [item.mName];
                        }else{
                            this.sortMembers[8].push(item.mName);
                        }
                    default:
                        break;
                }
            });
        }
    },
    methods: {
        getPId(v){
            this.pId = v;
        }
    },
    beforeCreate() {
        console.log('MemberItem.vue beforeCreate');
    },
    created() {
        console.log('MemberItem.vue Created');
    },
    beforeMount() {
        console.log('MemberItem.vue beforeMount');
    },
    mounted() {
        console.log('MemberItem.vue mounted');
        console.log(this.$route);
    },
    beforeUpdate() {
        console.log('MemberItem.vue beforeUpdate');
    },
    updated() {
        console.log('MemberItem.vue updated');
    },
    beforeDestroy() {
        console.log('MemberItem.vue beforeDestroy');
    },
    destroyed() {
        console.log('MemberItem.vue destroyed');
    },
}
</script>
